<template>
  <div>
    <div class="mdl-grid">
      <div class="mdl-cell mdl-cell--10-col mdl-cell--1-offset-desktop">
        <template v-for="(sugs, categoria) in sugestoes" v-if="sugs.length > 0">
          <h6 class="title">{{ categoria }}</h6>
          <hr>
          <ul class="demo-list-three mdl-list">
            <sugestao-item v-for="sugestao in sugs" :sugestao="sugestao" :categoria="categoria" :key="sugestao"></sugestao-item>
          </ul>
        </template>
      </div>
      <!-- <div class="mdl-cell mdl-cell--1-col mdl-cell--4-offset-desktop">1</div> -->
    </div>
  </div>
</template>

<script>
import SugestaoItem from '@/components/SugestaoItem'

export default {
  name: 'Index',
  data () {
    return {
      sugestoes: []
    }
  },
  mounted () {
    const vm = this
    this.$axios.get('/sugestoes')
    .then(function (response) {
      vm.sugestoes = response.data
    })
    .catch(function (error) {
      console.log(error);
    })

  },
  components: {
    SugestaoItem
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>



