<template>
  <li class="mdl-list__item mdl-list__item--three-line">
    <span class="mdl-list__item-primary-content">
      <i v-if="sugestao.imagem !== ''" class="material-icons mdl-list__item-avatar">person</i>
      <img v-else src="http://placehold.it/90x90" class="mdl-list__item-avatar">
      <span>{{ sugestao.titulo }}</span>
      <span class="mdl-list__item-text-body List-body" v-html="sugestao.descricao">
      </span>
    </span>
    <!-- <span class="mdl-list__item-secondary-content">
      <a class="mdl-list__item-secondary-action List-fav" href="#" >
          <i class="material-icons" v-if="voted">start</i>
          <i class="material-icons" v-else>star_border</i>
          <span class="List-count">{{sugestao.likes}}</span>
      </a>
    </span> -->
    <span class="mdl-list__item-secondary-content" :class="{'List-voted': voted}">
      <a class="mdl-list__item-secondary-action List-fav" :class="{'is-voted': voteType == 'like'}" href="#" @click="like(sugestao.id, 'l')">
        <span class="List-count">{{sugestao.likes}}</span>
        <i class="material-icons">thumb_up</i>
      </a>
      <a class="mdl-list__item-secondary-action List-fav" :class="{'is-voted': voteType == 'dislike'}" href="#" @click="dislike(sugestao.id, 'd')">
        <span class="List-count">{{sugestao.dislikes}}</span>
        <i class="material-icons">thumb_down</i>
      </a>
    </span>
  </li>
</template>

<script>
export default {
  data () {
    return {
      voted: false,
      voteType: ''
    }
  },
  props: ['sugestao', 'categoria'],
  methods: {
    like (id, tipo) {
      const vm = this
      if (!this.voted) {
        vm.$axios.get(`/votar/${id}/${tipo}`)
        .then(function (response) {
          vm.sugestao.likes++
          vm.voted = true
          vm.voteType = 'like'
        })
        .catch(function (error) {
          console.log(error);
        })
      }
    },
    dislike (id, tipo) {
      const vm = this
      if (!this.voted) {
        vm.$axios.get(`/votar/${id}/${tipo}`)
        .then(function (response) {
          vm.sugestao.dislikes++
          this.voted = true
          this.voteType = 'dislike'
        })
        .catch(function (error) {
          console.log(error);
        })
      }
    }
  }
}
</script>

<style scoped>
.List-fav {
  text-align: center;
  text-decoration: none !important;
  filter: grayscale(100%);
  transition: filter linear .1s;
}

.List-fav:not(.is-voted):hover {
  filter: grayscale(0);
}

.List-fav.is-voted {
  filter: grayscale(0);
}

.List-count {
  display: inline-block;
  font-size: 0.8em;
  margin-left: .5em;
  transform: translateY(-50%);
}

.List-body {
  max-width: 980px;
}


</style>




